@use 'base/variables' as v;
@use 'base/mixins' as m;

.output{
    background-color: v.$blanco;
    border-radius: 3.2rem;
    box-shadow: 0px 24px 32px -8px rgba(0, 0, 0, 0.08);
    
    display: flex;
    flex-direction: column;
    
    height: auto;
    width: 100%;
    max-width: 100%;
    margin: 2rem 0 1rem 0;
    padding: 1.5rem;

    img{
        display: none;
    }

    @include m.desktop {
        justify-content: center;
        width: 32rem;

        img{
            width: auto;
            height: auto;
            display: block;
        }
    }
}

.output-contenido{
    padding: 1rem;

    @include m.desktop {
        height: 50rem;
    }
}

.output-logo{
    padding: 1rem;
}

.output-texto{
    text-align: center;

    h2{
        margin: 0;
        font-size: 2.4rem;
    }

    p{
        font-size: 1.5rem;
    }
}

.output-encrypt{
    word-wrap: break-word;
    width: 100%;
    height: 100%;
}

.text-encrypt{
    margin: 0;
    font-size: 3.2rem;
    color: v.$gris-oscuro;

    @include m.desktop {
        display: block;
        width: 100%;
        height: 100%;
        overflow-y: auto;

        &::-webkit-scrollbar{
            width: .6rem;
        }
    
        &::-webkit-scrollbar-thumb{
            background-color: darken(v.$gris-medio, 10%);
            border-radius: 5rem;
    
            &:hover{
                background-color: darken(v.$gris-medio, 15%);
            }
        }
    }
}