@use 'base/variables' as v;
@use 'base/mixins' as m;

.header{
    margin: 2rem 0;
    width: 30%;

    img{
        width: 10rem;
        height: 4rem;
    }

    @include m.tablet {
        img{
            margin: 0 0 4rem 0;
        }
    }
    
    @include m.desktop {
        width: 100%;

        img{
            margin: 0 auto;
        }
    }


}