@use 'base/variables' as v;
@use 'base/mixins' as m;

.input{
    margin: 0 0 4rem 0;
    
    @include m.desktop{
        margin: 4rem 0 0 0;
    }

    .input-campo{
        width: 100%;
    }
}

.input-botones{
    max-width: 100%;
    width: 100%;

    @include m.tablet {
        display: flex;
        gap: 2rem;
        justify-content: space-between;
    }
}

.input-text{
    background-color: v.$gris-claro;
    border: none;
    color: v.$azul-negro;
    font-size: 3.2rem;
    padding: 2rem;
    resize: none;
    width: 100%;
    height: 40rem;

    overflow: hidden;
    word-wrap: break-word;

    @include m.desktop {
        overflow: scroll;
    }

    &::placeholder{
        color: v.$azul-negro;
    }

    &:focus{
        outline: none;
    }

    &::-webkit-scrollbar{
        width: .6rem;
    }

    &::-webkit-scrollbar-thumb{
        background-color: darken(v.$gris-medio, 10%);
        border-radius: 5rem;

        &:hover{
            background-color: darken(v.$gris-medio, 15%);
        }
    }
}

.input-mensaje{
    p{
        color: v.$gris-oscuro;
        margin: 2rem 0;
        font-size: 1.5rem;
    }
}