html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Inter", sans-serif;
  font-size: 1.6rem;
  background-color: #F3F5FC;
}
@media (min-width: 992px) {
  body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1.5rem;
    height: 100vh;
  }
}

p {
  font-size: 2rem;
  color: #000000;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.oculto {
  display: none;
}

.error {
  color: red !important;
}

.boton-primario {
  background-color: #0A3871;
  border-radius: 2.4rem;
  border: 1px solid #0A3871;
  color: #FFFFFF;
  cursor: pointer;
  padding: 2rem 9rem;
  transition: background-color 0.5s ease;
  width: 100%;
  margin-bottom: 2rem;
}
.boton-primario:hover {
  background-color: #062142;
}
@media (min-width: 768px) {
  .boton-primario {
    margin-bottom: 0;
  }
}

.boton-secundario {
  background-color: #F3F5FC;
  border-radius: 2.4rem;
  border: 1px solid #0A3871;
  color: #0A3871;
  cursor: pointer;
  padding: 2rem 9rem;
  transition: background-color 0.5s ease;
  width: 100%;
}
.boton-secundario:hover {
  background-color: #cad3f2;
}

.boton-terciario {
  background-color: #FFFFFF;
  border-radius: 2.4rem;
  border: 1px solid #0A3871;
  color: #0A3871;
  cursor: pointer;
  padding: 2rem 9rem;
  transition: background-color 0.5s ease;
  width: 100%;
}
.boton-terciario:hover {
  background-color: #e6e6e6;
}

.header {
  margin: 2rem 0;
  width: 30%;
}
.header img {
  width: 10rem;
  height: 4rem;
}
@media (min-width: 768px) {
  .header img {
    margin: 0 0 4rem 0;
  }
}
@media (min-width: 992px) {
  .header {
    width: 100%;
  }
  .header img {
    margin: 0 auto;
  }
}

@media (min-width: 992px) {
  .contenido {
    display: grid;
    grid-template-columns: 1fr 4fr 2fr;
    margin-top: 2rem;
    gap: 3rem;
  }
}
.input {
  margin: 0 0 4rem 0;
}
@media (min-width: 992px) {
  .input {
    margin: 4rem 0 0 0;
  }
}
.input .input-campo {
  width: 100%;
}

.input-botones {
  max-width: 100%;
  width: 100%;
}
@media (min-width: 768px) {
  .input-botones {
    display: flex;
    gap: 2rem;
    justify-content: space-between;
  }
}

.input-text {
  background-color: #F3F5FC;
  border: none;
  color: #0A3871;
  font-size: 3.2rem;
  padding: 2rem;
  resize: none;
  width: 100%;
  height: 40rem;
  overflow: hidden;
  word-wrap: break-word;
}
@media (min-width: 992px) {
  .input-text {
    overflow: scroll;
  }
}
.input-text::placeholder {
  color: #0A3871;
}
.input-text:focus {
  outline: none;
}
.input-text::-webkit-scrollbar {
  width: 0.6rem;
}
.input-text::-webkit-scrollbar-thumb {
  background-color: #b8c5d5;
  border-radius: 5rem;
}
.input-text::-webkit-scrollbar-thumb:hover {
  background-color: #a8b8cc;
}

.input-mensaje p {
  color: #495057;
  margin: 2rem 0;
  font-size: 1.5rem;
}

.output {
  background-color: #FFFFFF;
  border-radius: 3.2rem;
  box-shadow: 0px 24px 32px -8px rgba(0, 0, 0, 0.08);
  display: flex;
  flex-direction: column;
  height: auto;
  width: 100%;
  max-width: 100%;
  margin: 2rem 0 1rem 0;
  padding: 1.5rem;
}
.output img {
  display: none;
}
@media (min-width: 992px) {
  .output {
    justify-content: center;
    width: 32rem;
  }
  .output img {
    width: auto;
    height: auto;
    display: block;
  }
}

.output-contenido {
  padding: 1rem;
}
@media (min-width: 992px) {
  .output-contenido {
    height: 50rem;
  }
}

.output-logo {
  padding: 1rem;
}

.output-texto {
  text-align: center;
}
.output-texto h2 {
  margin: 0;
  font-size: 2.4rem;
}
.output-texto p {
  font-size: 1.5rem;
}

.output-encrypt {
  word-wrap: break-word;
  width: 100%;
  height: 100%;
}

.text-encrypt {
  margin: 0;
  font-size: 3.2rem;
  color: #495057;
}
@media (min-width: 992px) {
  .text-encrypt {
    display: block;
    width: 100%;
    height: 100%;
    overflow-y: auto;
  }
  .text-encrypt::-webkit-scrollbar {
    width: 0.6rem;
  }
  .text-encrypt::-webkit-scrollbar-thumb {
    background-color: #b8c5d5;
    border-radius: 5rem;
  }
  .text-encrypt::-webkit-scrollbar-thumb:hover {
    background-color: #a8b8cc;
  }
}

.footer {
  margin: 0 auto;
  background-color: #0A3871;
  text-align: center;
  padding: 2rem;
  width: 80%;
  border-radius: 2rem;
}
.footer p {
  font-size: 2rem;
  color: #FFFFFF;
  margin: 0;
}