@use 'variables' as v;

@mixin phone{
    @media (min-width: v.$phone) {
       @content; 
    }
}

@mixin tablet{
    @media (min-width: v.$tablet) {
       @content; 
    }
}

@mixin desktop{
    @media (min-width: v.$desktop) {
       @content; 
    }
}

@mixin desktopXL{
    @media (min-width: v.$desktopXL) {
       @content; 
    }
}

@mixin grid($columns, $spacing){
    display: grid;
    grid-template-columns: repeat($columns, 1fr);
    gap: $spacing;
}

@mixin resetear-lista {
    margin: 0;
    padding: 0;
    list-style: none;
}

@mixin boton($bgColor, $fnColor){
    background-color: $bgColor;
    border-radius: 2.4rem;
    border: 1px solid v.$azul-negro;
    color: $fnColor;
    cursor: pointer;
    padding: 2rem 9rem;
    transition: background-color .5s ease;
    width: 100%;

    &:hover{
        background-color: darken($bgColor, 10%);
    }
}