@use 'base/variables' as v;
@use 'base/mixins' as m;

.boton-primario{
    @include m.boton(v.$azul-negro, v.$blanco);
    
    margin-bottom: 2rem;
    
    @include m.tablet {
        margin-bottom: 0;
    }
}

.boton-secundario{
    @include m.boton( v.$gris-claro, v.$azul-negro);
}

.boton-terciario{
    @include m.boton( v.$blanco, v.$azul-negro);
}